nav{
    background: rgba(0, 0, 0,0.3);
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 1rem;
    display: flex;
    gap: 0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
}
nav a {
    background: transparent;
    padding: 0.8rem;
    border-radius: 50%;
    display: flex;
    color: var(--orange);
    font-size: 1.1rem;
}
 nav a:hover {
    color: rgb(99, 98, 98) !important;
 }
nav a.active {
    background: var(--orange);
    color: whitesmoke;
}




@media screen and (max-width: 280px) {
    nav{
        padding: 0.4rem 1.2rem;
        gap: 0.1rem;
        
    }
    }