header {
  height: 95vh !important;
  padding: 5rem;
  overflow: hidden;
  background-color: black;
  margin: 1em;
}

.header__container {
  text-align: center;
  height: 100%;
  position: relative;
  background: url('../../assets/images/header.avif') center no-repeat;
  background-size: cover;
  padding: 10% 3em;
}

h5,
h1 {
  color: var(--orange);
}

.scroll__down {
  text-decoration: none;
  color: var(--orange);
  border: 1px solid whitesmoke;
  padding: .2em;
}

.scroll__down:hover {
  color: whitesmoke;
}

/*===================== CTA ====================*/
.cta {
  margin-top: 2.5rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}

.cta a:hover {
  color: var(--orange);
  background: none !important;
}

/*===================== Header SOCIAL  ====================*/

.header__socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem;
}

.header__socials::after {
  content: "";
  width: 1px;
  height: 2rem;
  background-color: var(--dark);
}

.header__socials a {
  border: 1px solid whitesmoke;
  padding: .2em;

  color: whitesmoke !important;
  /* color : rgb(175, 137, 124) */

}

.header__socials a:hover {
  /* color: whitesmoke !important; */
  color: rgb(175, 137, 124) !important;
  border-color: rgb(175, 137, 124) !important;

}


/*================ SCROLL DOWN ===================*/
.scroll__down {
  position: absolute;
  right: -2.3rem;
  bottom: 5rem;
  transform: rotate(90deg);
  font-weight: 300;
  font-size: 0.9rem;
}

/*============= MEDIA QUERIES (MEDIUM DEVICES) ==============*/

@media screen and (max-width: 1024px) {
  header {
    height: 68vh;
  }

  .header__container{
    padding: 12% 3em;
  }
}

@media screen and (max-width: 773px) {
  header {
    height: 100vh;
    padding: 1.5em;
  }
.header__container{
  padding: 25% 3em;
}
}

@media screen and (max-width: 502px) {
  .cta{flex-direction: column;
  
  }
  .header__socials {
    flex-direction: row;
    bottom: 4rem;
  }
  .scroll__down{
    display: none;
  }
  
}

@media screen and (max-width: 280px) {
header{
margin: .4em;
padding: 1em;

}
.header__container{
  padding: 45% .7em ;
}

}